import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'


class MetaLink extends Component {
    constructor(props){
        super()
   this.state={
        metalink:"",
        image:"https://i.imgur.com/TaFS2Rj.png",
        label:""
    }
}
 
    componentDidMount() {
       
    }
      
    async setLabel() {
        try{
            let labe=await utils.loadMetaParam(this.props.address,"name")
            let ima=await utils.loadMetaParam(this.props.address,"image")
            let dark=await utils.loadMetaParam(this.props.address,"dark")
            let text=await utils.loadMetaParam(this.props.address,"text")
            document.getElementById(this.props.address).style.background=dark
            document.getElementById(this.props.address).style.color=text
            document.getElementById(this.props.address).textContent=cleanText(labe)
            document.getElementById("image"+this.props.address).setAttribute("src",utils.uri2url(ima))
            this.setState({label:labe})
            }catch(e){
            document.getElementById(this.props.address).textContent=cleanText(this.props.address)
            document.getElementById("image"+this.props.address).setAttribute("src","https://i.imgur.com/TaFS2Rj.png")
            }
    }

  

    render(){
        //alert(this.props.address)
if(this.props.address&&(this.state.label=="")){
    this.setLabel()
}
        return ( 
            <div className="minimetalink dark darkblue" onClick={() => window.open("/cyphervault/?account="+this.props.address)}>
                <div className="minimetalink_image_container dark darkblue">
                    <img className="minimetalink_image" id={"image"+this.props.address} src=""></img>
                </div>
                <div className="minimetalink_label_container dark darkblue" id={this.props.address}>
                
                </div>
            </div>
        );
    }


}

function cleanText(str){
    return str.replace(/[<>"]/gi, '');
}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

// <PersonalFeed update={this.props.update} vault={this.state.vault} avatar={this.state.avatar}></PersonalFeed>
           

const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default MetaLink