import React, { Component } from 'react'
import Lipshead from '../Lipshead/Lipshead';
import Pad from '../Pad/Pad';
import Info from '../Info/Info';
import './css.css';
import utils from './../../library'


class Home extends Component { 
    constructor(props) {
        super(props)
        this.state = { info:"" ,panel: 'Splash' , token : '0x801F90f81786dC72B4b9d51Ab613fbe99e5E4cCD',id:'0'}

    }

    update(panelx,tokenx){
        this.setState({panel:panelx,token:tokenx})
        this.closeInfo("")
        if(panelx!="Avatar")window.history.pushState('new_panel', panelx, "/");
        console.log("changed page!",panelx);
     }

   info(field){
      this.setState({info:field})
document.getElementById("info_table").style.display="block"
document.getElementById("superpad").style.display="none"
   }

   closeInfo(field){
      this.setState({info:""})
      document.getElementById("info_table").style.display="none"
      document.getElementById("superpad").style.display="block"
   }
   

    render(){ 
        
        
       let panel=this.state.panel
       let token=this.state.token
       let id=this.state.id
       if(utils.getUrlVars()["cx"]){
          panel="create"
          token=utils.getUrlVars()["cx"]
          id=0
          if(utils.getUrlVars()["id"]){
            id=utils.getUrlVars()["id"]
         }
         }
        
       if(utils.getUrlVars()["account"]){
          panel="Avatar"
          token=utils.getUrlVars()["account"]
         }

         if(utils.getUrlVars()["tag"]){
            panel="Feed"
            token=utils.getUrlVars()["tag"]
           }

          let to=utils.getUrlVars()["to"]
        
        return (
        <div id="home" className="dark darkblue">
           <Lipshead update={this.update.bind(this)}></Lipshead>
           <Pad panel={panel} info={this.info.bind(this)} token={token} id={id} to={to} update={this.update.bind(this)}/>
           <Info closeInfo={this.closeInfo.bind(this)} info={this.state.info}></Info>
        </div>);
    }
}



export default Home