import React, { Component } from 'react'
import './css.css';
import utils from '../../library'

let informations={
    "standard":["🍓 STANDARDS","<b>NFT ETH_ITEM</b><br><br>A new universal standard putting together erc1155 and erc20 standards.<br>A bit more expensive. Use it to mint unique/multiple items or tokenized ones.<br>The interoperable version you get can be sold on uniswap like normal tokens!<br>Perfect for gaming.<br><br><br><b>NFT ERC721</b><br><br>The classic standard NFT entity. Use it to mint unique items."],
    "type":["🍌 TYPE","<b>🎨 IMAGE</b><br><br>Accepts any kind of image.<br>Animated gifs are allowed.<br>Before minting ensure yourself the image is actually loaded by minting the token on the test platform (Ethereum Ropsten Testnet)<br><br><br><b>🎲 VOX FILES</b><br><br>Tested with <b>Magica Voxels .vox</b> files.<br>Before minting ensure yourself the vox file is actually loaded by minting the token on the test platform (Ethereum Ropsten Testnet)<br><br><br><b>🎷 SOUNDS AND EFFECTS</b><br><br>Accepts <b>.mp3</b> files.<br>Everything is decentralized and stored on ipfs, reason why big audio files may take time to be downloaded.<br>This Factory is meant to produce and share little sounds and effects, or short plays.<br>Before minting ensure yourself the sound is actually loaded by minting the token on the test platform (Ethereum Ropsten Testnet)<br><br><br><b>🎬 VIDEO</b><br><br>Accepts <b>.mp4</b> files.<br>Everything is decentralized and stored on ipfs, reason why big video files may take time to be downloaded.<br>This Factory is meant to shoot little animations or short meme oriented (tiktok alike) shootings.<br>Before minting ensure yourself the video is actually loaded by minting the token on the test platform (Ethereum Ropsten Testnet)"],
    "amount":["🌾 AMOUNT","The amount of copies you want to mint for a given item.<br>ERC721 standard allows a single copy."],
    "name":["👻 NAME","The name of this item."],
    "description":["📜 DESCRIPTION","A text describing the artwork or the item."],
    "symbol":["🃏 SYMBOL","A symbol for your token. (something like ETH, emojis may not be recognized by some platforms, use them at your own risk)"],
    "info":["📰 INFO","Collateral informations for this item.<br>Can contain any kind of information, such as links or notes/comments."],
    "tags":["🏷️ TAGS","Add tags for cataloguing reasons. Mandatory."],
    "author":["☠️ AUTHOR","The name of the creator of this item."],
    "cover":["🖼️ IMAGE","The cover image shown around the internet.<br>Usually this contains a mid-quality image to allow fast browsing.<br>This is used as the cover for any family of NFT (2D, 3D, music, videos etc.)<br>The real high quality files (4k or 8k or more) are contained in the 'HigRes Image Version'."],
    "image_data":["🖼️ HIG RES IMAGE VERSION","The high quality version of your file.<br>Memory committing, this file can be a '4k or 8k or more'"],
    "audio":["🎷 AUDIO","The audio file you are going to mint. Shouldn't be too big.<br>Make a test on Ropsten Testnet to see the performance (may change depending on the day time)."],
    "video":["🎬 VIDEO","The video file you are going to mint. Shouldn't be too big.<br>Make a test on Ropsten Testnet to see the performance (may change depending on the day time)."],
    "vox":["🎲 VOX FILES","The .vox file you are going to mint. Shouldn't be too big.<br>(tested with Magica Voxel files.)<br>Make a test on Ropsten Testnet to see the performance (may change depending on the day time)."],
}
class Info extends Component {
   

    componentDidUpdate(prevProps){
        if (prevProps.info !== this.props.info)this.setInfo()
    }

    setInfo(){
        let info=this.props.info
        let eleT=document.getElementById("infotitle")
        let ele=document.getElementById("infocontent")
        if(info!=""){
            eleT.innerHTML=informations[this.props.info][0]
            ele.innerHTML=informations[this.props.info][1]  
        }
    }

    render(){
        return ( 
        <div>

        <div id="info_table" className="light lightblue">
        <div id="infotitle"></div>
        <div id="infocontent"></div>
            <div id="infobackbutton" className="dark darkblue" onClick={() => this.props.closeInfo()}>BACK</div>
            
        </div>
      <div className="clear"></div>
        </div>
        );
    }


}




export default Info