function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}

function getTypeIcon(t) {
    var tt=""
    if(t=="vox")tt="🎲"
    if(t=="audio")tt="🎻"
    if(t=="visual")tt="🎨"
    if(t=="video")tt="🎬"
    return tt;
}

function getImage() {
    var t=getUrlVars()
    var tt=""
    if(t["curriculum"]=="entertainer")tt="🤹"
    if(t["curriculum"]=="pr")tt="💞"
    if(t["curriculum"]=="townkey")tt="🔑"
    if(t["curriculum"]=="medal")tt="🥇"

    if(t["curriculum"]=="cup")tt="🏆"
    if(t["curriculum"]=="clown")tt="🤡"
    if(t["curriculum"]=="unicorn")tt="🦄"
    if(t["curriculum"]=="shit")tt="💩"

    if(t["curriculum"]=="idea")tt="💡"
    if(t["curriculum"]=="whitehat")tt="🎓"
    if(t["curriculum"]=="blackhat")tt="🔫"
    if(t["curriculum"]=="help")tt="🚑"
    return tt;
}

function multiply(a, b) {
    if ((a | 0) == 0 || (b | 0) == 0) {
        return '0';
    }

    a = a.split('').reverse();
    b = b.split('').reverse();
    var result = [];

    for (var i = 0; a[i] >= 0; i++) {
        for (var j = 0; b[j] >= 0; j++) {
            if (!result[i + j]) {
                result[i + j] = 0;
            }

            result[i + j] += a[i] * b[j];
        }
    }

    for (var i = 0; result[i] >= 0; i++) {
        if (result[i] >= 10) {
            if (!result[i + 1]) {
                result[i + 1] = 0;
            }

            result[i + 1] += parseInt(result[i] / 10);
            result[i] %= 10;
        }
    }

    return result.reverse().join('');
}

function getName() {
    return getUrlVars()["curriculum"];
}

function getAddress() {
    return getUrlVars()["cx"];
}
