import React, { Component } from 'react'
import './css.css';
import MetaLink from '../MetaLink/MetaLink'
import utils from '../../library'


class Pioneers extends Component {
   
    MetaList = () => {
        let pioneers=[
        "0x6692bf36afe9d3954f5c92b428f9068e8ee59b02",
        "0xca98e45e09c8690488d1f7054f0555fff1225408",
        "0xf7e72865cf3e2c4f6970f628b2e84bdf3edca222",
        "0x4cb0e534e9c6c6081d547d076dca54c0fd0e47d0",
        "0xbd16452539668d366d768fd1f6128aeccbc4629c",
        "0xb6e2a772e5cc14bbf10bd7e8b5a762b4f6994d31"
]
        let listItems = pioneers.map((addr) =>
          // Correct! Key should be specified inside the array.
          <MetaLink address={addr} key={Math.random()}/>
      
        );
        return (
          <>
            {listItems}
          </>
        );
      }


    render(){
        console.log(this.props.update) 
        utils.setHomeColors()
        return ( 
        <div id="Pioneers_flyer">
            <span id="Pioneers_image" aria-labelledby="jsx-a11y/accessible-emoji" role="img">👑</span>
            <div id="Pioneers_mainlabel">Early Cyphers List</div>
<div className="medals">1/10 =🏆 &nbsp;&nbsp; 11/25 = 🏅 &nbsp;&nbsp; 26/50 = 🎖️</div>
            <div className="Pioneers_container">
        {this.MetaList()}
</div>
<div className="Pioneers_sublabel">
            <i>people who contributed to testing the platform<br></br>NFT Badges will be distributed later!</i>
            </div>
            <div className="Pioneers_button dark darkblue text white" onClick={() => this.props.update("Ufactory")}>back</div>
        </div>
        );
    }


}




export default Pioneers