import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
import utils from '../../library'
import blockies from '../../blockies'


class Ufactory extends Component {
    state={
        vault:"",
        standard:"item",
        counter:"0",
        loading:"0",
        flyer:"1"
    }
     
    async setVault(_vault){
        
        document.getElementById(this.state.flyer).outerHTML=""
        let fly=""+(parseInt(this.state.flyer)+1)
        let d=document.createElement("div")
        d.setAttribute("id",fly)
        d.setAttribute("class","factory_flyer")
        document.getElementById("flyer_container").append(d)
        await this.setState({vault:_vault,flyer:fly})
        document.getElementById("ufactory_mainlabel").textContent="DIR 🎪"
        this.loadFeed("default",'all',_vault,this.state.standard)
    }

    async setStandard(stn){
        document.getElementById(this.state.flyer).outerHTML=""
        let fly=""+(parseInt(this.state.flyer)+1)
        let d=document.createElement("div")
        d.setAttribute("id",fly)
        d.setAttribute("class","factory_flyer")
        document.getElementById("flyer_container").append(d)
        await this.setState({standard:stn,flyer:fly})
        document.getElementById("ufactory_mainlabel").textContent="DIR 🎪"
        this.loadFeed("default",'all',this.state.vault,stn)
    }

    async waiting(x,standard,event,i,element){
        await utils.loadAndDisplayAsync(standard,event,i,element)
        let t=parseInt(x.state.loading)+1
        x.setState({loading:t})
    }


    componentDidMount(){
     
        utils.setHomeColors()
        this.loadFeed("default",'all',this.state.vault)
       }
    
    async loadFeed(blck,who,vault,standard){
           let vaultx
        if(vault=="visual"){
        document.getElementById("ufactory_mainlabel").textContent=vault+"🎨"
        }
        if(vault=="vox"){
        document.getElementById("ufactory_mainlabel").textContent=vault+"🎲"
        }
  
        if(vault=="audio"){
        document.getElementById("ufactory_mainlabel").textContent=vault+"🎻"
        }
 
        if(vault=="video"){
        document.getElementById("ufactory_mainlabel").textContent=vault+"🎬"
        }
      
      let ele=await document.getElementById(this.state.flyer)
      
        let count=await this.loadUnofficialEvents(blck,who,vault,standard,ele)
        //let t=parseInt(this.state.loading)+count.length
        this.setState({counter:count.length,loading:"0"})
       }
      

       async loadUnofficialEvents(blck,who,vault,standard,element){
        //console.log("element",element)
        let feed="z"
        let waiting= this.waiting
        let x=this
        if(vault!="")

        await window.ethereum.enable().then(async function (accounts) { 
           
                console.log(vault,standard)
                const { REACT_APP_EMITTERABI } = process.env; 
                try{

                let mevents = new web3.eth.Contract(JSON.parse(REACT_APP_EMITTERABI),utils.getVault(vault,standard))
                
                let block=(blck=="default")?parseInt(await utils.getBlock())-100000:parseInt(blck)
                let eventz 
                console.log("here","2")
                    let arr=[who]
                    arr=who
                    eventz = await mevents.getPastEvents('ncx',
                    { fromBlock: block, toBlock: 'latest'
                    } , async function(error, event){ 
                        feed=event
                        console.log("loadUnofficialEvents")
                        console.log(event)
    
                        for(let i=event.length-1;i>=0;i--){
                            waiting(x,standard,event,i,element)
                        }
                    })
                
                    console.log("here","3")
            }catch(e){console.log("loadUnofficialEvents "+e.toString())}
        })
        return feed
    }


    
    render(){
        console.log(this.props.update) 
        let count=""+this.state.counter
        return ( 
        <>
        <div id="ufactory_header">
        {(this.state.vault!="")?
        <>
        <div id="ufactory_backbutton" onClick={() => this.setVault("")}>back</div>
        <div className={(this.state.standard=="item")?"ufactory_setstandard_button dark darkblue text white":"ufactory_setstandard_button text white"} onClick={() => this.setStandard("item")}>ITEMS</div>
        <div className={(this.state.standard=="erc721")?"ufactory_setstandard_button dark darkblue text white":"ufactory_setstandard_button text white"} onClick={() => this.setStandard("erc721")}>ERC721</div>
        </>
        :
        <></>
        }
        <div id="ufactory_mainlabel" className="text white">DIR 🎪</div>
        {(this.state.vault!="")?<div id="ufactory_maincounter" className="dark darkblue text white">{count}</div>:<></>}
        {(this.state.vault!="")?(parseInt(this.state.loading)<parseInt(this.state.counter))?<div id="ufactory_loadingcounter" className=" text white">loading {parseInt(this.state.loading)} of </div>:<></>:<></>}
        
        </div>
        <div className="clear"/>
        {(this.state.vault!="")?
        <>
        </>
        :
        <>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("visual")}>🎨 VISUAL</div>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("vox")}>🎲 VOX</div>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("audio")}>🎻 AUDIO</div>
        <div className="ufactory_section_button dark darkblue" onClick={() => this.setVault("video")}>🎬 VIDEO</div>
        </>
        }
        <div id="flyer_container">
        <div id={this.state.flyer} className="factory_flyer">  
        </div>   
        </div>
        </>
        );
    }


}



const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")

export default Ufactory