import React, { Component } from 'react'
import Web3 from 'web3'
import ipfs from '../../ipfs'
import utils from '../../library'
import Base64Binary from './../../base64'
import './css.css';



class Meta extends Component {
constructor(props){
    super()
  this.state={cover:"",light:"white",dark:"red",text:"blue",image:""}

}

componentDidMount(){
    document.getElementById("personal_submit_meta").style.opacity="1"
    document.getElementById("wait").style.display = "none";
    this.loadMeta()
}


async loadMeta(){
  
        let addr=""
        let _dark
        let _light
        let _text
        await window.ethereum.enable().then(async function (accounts) {
            let metafound=false
            let mtdt
            const { REACT_APP_METAABI } = await process.env;
            const { REACT_APP_META } = await  process.env;
            
            addr=accounts[0]
            const metadata = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
            mtdt=await metadata.methods.meta(addr).call()
            if(mtdt.length>=0)metafound=true
            console.log("MTDT>>>>Z> "+addr+" "+REACT_APP_META,mtdt)
            
            if(!metafound){
            addr=accounts[0]
            const metadata = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
            mtdt=await metadata.methods.meta(addr).call()
            }
           // document.getElementById("Shop_twitter_pan").style.display='none'
            //document.getElementById("Shop_discord_pan").style.display='none'
      
            if(mtdt.length>0){
                console.log("MTDT",mtdt)
                await fetch(formatURL(mtdt)).then(response => response.json()).then(
                response => {
                    console.log(JSON.stringify(response))
                    try{
                        try{document.getElementById("Shop_meta_setname").value=cleanText(response["name"])}catch(e){}
                        try{document.getElementById("setmeta_setdesc").value=cleanText(response["description"])}catch(e){}
                        try{document.getElementById("meta_cover_canvas").setAttribute("src",utils.uri2url(response["image"]))}catch(e){}
                        try{document.getElementById("newmeta_setlinks").value=cleanText(response["folder"])}catch(e){}
                        _dark=response["dark"]
                        _light=response["light"]
                        _text=response["text"]
                        console.log("TEXT",_text)
                        utils.setBackground(response)
                        document.getElementById("personal_name").textContent=cleanText(response["name"])
                        document.getElementById("personal_description").textContent=cleanText(response["description"])
                    }catch(e){}
                    let back=response["image"]
                    if(back=="")back='https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/27592dd5-bdf2-4e90-9e17-852077de3dd0/d2aytcz-fd1a408a-541d-465d-9f8d-9c662b5ba6db.jpg/v1/fill/w_900,h_600,q_75,strp/fading_blue_grunge_by_dyingbeautystock.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..JSbyhtv1aQd2O8AzG2x1UTZLfyNdtwGNtNSgzKCDFTE'
                    
                    //document.getElementById("Shop_header_image").style.backgroundImage='url('+back+')'
        
                    
   
                })
            }

        })
        if(!_dark)_dark="#f5eaff"
        if(!_light)_light="#ffffff"
        if(!_text)_text="#bb7df8"
        utils.setBackgroundColors(_dark,_light,_text)
    await this.setState({light:_light,dark:_dark,text:_text})
    document.getElementById("Shop_meta_setdark").value=_dark
    document.getElementById("Shop_meta_setlight").value=_light
    document.getElementById("Shop_meta_settext").value=_text
}


imageHandler  = (e) => {
    //document.getElementById("Shop_meta_setmeta_button").style.opacity = "0.3";
    //document.getElementById("wait").text="loading to IPFS, please wait..."
    //document.getElementById("wait").style.display = "block";
    console.log("!")
    const reader = new FileReader();
    let t=this
    let _image
reader.onload = async() => {
    console.log("!!")
    console.log(reader.result)
    if(reader.readyState === 2){
        var img = new Image()
        let myCover = new Image(300, 300);
                const canvas = document.createElement('canvas');
                const ctx = canvas.getContext('2d');
                canvas.width = myCover.width;
                canvas.height = myCover.height;
                this.setState({cover:reader.result})

            img.onload = async function() {
                ctx.drawImage(this, 0, 0, 300, 300);
            let im=reader.result
            let ext=im.substr(11,im.indexOf(";base64")-11)

            let ts='data:image/'+ext+';base64,'
            ///data:image\/;base64,/
                let baseSixtyFour=im.replace(ts,'');
                let byteArray = Base64Binary.decodeArrayBuffer(baseSixtyFour);
                const buffer0 = await Buffer.from(byteArray);
                console.log("IPFS0");
                
                
                await ipfs.add(buffer0).then(async (response) => {
                    _image="ipfs://ipfs/"+response["path"].toString()
                   
                    //document.getElementById("Shop_meta_setmeta_button").style.opacity = "1.0";
                    //document.getElementById("wait").style.display = "none";
                    //document.getElementById("wait").text="loading to IPFS, please wait..."
                    document.getElementById("meta_cover_canvas").setAttribute("imdata",_image)
                   
                    document.getElementById("Avatar_face_container").innerHTML=""
                    let avat = document.createElement('img')
                    avat.setAttribute('src',utils.uri2url(_image))
                    avat.setAttribute('class',"avatar_banner")
                    document.getElementById("Avatar_face_container").append(avat)
                    console.log(_image);
                }).catch((err) => {       
                    console.error(err)   
                })
                
          }
        
        img.src = reader.result
        
                
                
    }
    this.setState({image:_image})
}
reader.readAsDataURL(e.target.files[0])

//let baseSixtyFour=canvas.toDataURL().replace(/data:image\/png;base64,/,'');
                
console.log("!")

}

async save(){
    let addr=""
    
    document.getElementById("wait").style.display = "block";
    document.getElementById("note").style.display = "block";
    document.getElementById("wait").textContent = "sign Metamask tx and wait";
    let links=cleanText(document.getElementById("newmeta_setlinks").value)
    let name=cleanText(document.getElementById("Shop_meta_setname").value)
    let desc=(document.getElementById("setmeta_setdesc").value)
    let imag=document.getElementById("meta_cover_canvas").getAttribute("imdata")
    console.log("save image",imag)
    let dark=document.getElementById("Shop_meta_setdark").getAttribute("col")
    console.log("dark",dark)
    let light=document.getElementById("Shop_meta_setlight").getAttribute("col")
    console.log("light",light)
    let textcolor=document.getElementById("Shop_meta_settext").getAttribute("col")
    console.log("textcolor",textcolor)
    await window.ethereum.enable().then(async function (accounts) { 
        
            addr=accounts[0]
        
       
        let str='{"name":"'+name+'","description":"'+desc+'","image":"'+imag+'","dark":"'+dark+'","light":"'+light+'","text":"'+textcolor+'","folder":"'+links+'"}'
        var enc = new TextEncoder(); // always utf-8
        const buffer = await Buffer.from(str);
        console.log("IPFS1");
        await ipfs.add(buffer).then(async (response) => {
          console.log(response["path"]);
          const { REACT_APP_METAABI } = process.env;
          const { REACT_APP_META } = process.env;
          const meta = new web3.eth.Contract(JSON.parse(REACT_APP_METAABI), REACT_APP_META) 
          await meta.methods.setWalletMeta(response["path"].toString()).send({from: accounts[0] , gas: 123456 })
document.getElementById("personal_submit_meta").style.opacity="0.3"
document.getElementById("wait").textContent = "complete!";
document.getElementById("note").style.display="none"
        }).catch((err) => {
        
          console.error(err)
       
        })
       
      
})  
this.loadMeta()
}

async setDark(){
    utils.setDarkMeta(document.getElementById("Shop_meta_setdark").value)
    document.getElementById("Shop_meta_setdark").setAttribute("col",document.getElementById("Shop_meta_setdark").value)
}
async setLight(){
    utils.setLightMeta(document.getElementById("Shop_meta_setlight").value)
    document.getElementById("Shop_meta_setlight").setAttribute("col",document.getElementById("Shop_meta_setlight").value)
}
async setText(){
    utils.setTextMeta(document.getElementById("Shop_meta_settext").value)
    document.getElementById("Shop_meta_settext").setAttribute("col",document.getElementById("Shop_meta_settext").value)
}

check(x) {
    let elements = document.getElementsByClassName(x);
    for (var i = 0; i < elements.length; i++) {
        elements[i].style.backgroundColor="red";
    }
}

    render(){
let target="avatar"
if(getUrlVars()["shop"])target="shop"
        return ( 
            <>
        <div className="Shop_meta_row">        
            <div className="personal_meta_panel dark darkblue text white">
  
               <span className=" right">gallery metadata ✨</span>
               <div className="clear"></div>
               <br></br>
               <div>
               <span>💀 name:</span><br></br>
               <input id="Shop_meta_setname" className="Shop_meta_setval light lightblue text white" type="text" ></input>
               </div>
               <div>
               <span>🏷️ description:</span><br></br>
               <textarea id="setmeta_setdesc" className="setmeta_setval light lightblue text white" type="text" ></textarea>
               </div>
               <div>
               <span>🖼️ Image (web use):</span><br></br>
                <input className="metacontent3" type="file" imdata="" id="Shop_meta_setcover" name="cover" accept="image/*" onChange={this.imageHandler}></input>
                <img id="meta_cover_canvas" imdata="" className="textborder" width="300" src={this.state.cover} height="300"></img>
                <div className="clear"></div>
               </div>
               <br></br>
               <div>
               <span>🎨 colors (dark and light and text):</span><br></br>
               <input type="color" pseudo="-webkit-color-swatch" background={this.state.dark} id="Shop_meta_setdark" className="gallery_meta_setval_color dark darkblue text white" onChange={() => this.setDark()}></input>
               <input type="color" pseudo="-webkit-color-swatch" background={this.state.light} id="Shop_meta_setlight" className="gallery_meta_setval_color light lightblue text white" onChange={() => this.setLight()}></input>
               <input type="color" pseudo="-webkit-color-swatch" background={this.state.text} id="Shop_meta_settext" className="gallery_meta_setval_color textback white" onChange={() => this.setText()}></input>
               <div className="clear"></div>
               </div>
               <div>
                    <span>🍬 gallery links (0xAddress,0xAddress,...):</span><br></br>
                    <textarea id="newmeta_setlinks" className="Shop_meta_setval light lightblue text " type="text" ></textarea>
               </div>
               <div className="clear"></div>
               
               <input id="personal_submit_meta" className="personal_setmeta_button light lightblue text white" type="button" value="🏹 save" onClick={() => this.save()}></input>
               <div className="wait" id="wait"></div>
               <div className="note" id="note"><i>big files may take time</i></div>
               <div className="clear"></div>
               <br></br><br></br>     
            </div>
        </div>
       </>
            
        );
    }


}

function jumpsocial(add){
    window.open(add)
}

function cleanText(str){
    let _str=str
    _str=_str.replaceAll("\n","")
    _str=_str.replaceAll("\r","")
    return _str.replace(/[<>"]/gi, '');
}

function formatURL(url){
    return("https://gateway.ipfs.io/ipfs/"+url);
  }

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}


        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default Meta